import axios from "axios";
import { userDataType } from "../types/types";

type HeaderLoginResponseType = {
    data: {
        id: number
        email: string
        login: string
    }
    resultCode: number
    messages: Array<string>
}
type LoginResponseType = {
    data: {
        userId: number
    }
    resultCode: number
    messages: string
}

type getUsersType = {
    items: userDataType
    totalCount: number
    error: string
}

type followUnfollowType = {
    data: {}
    resultCode: number
    messages: string
}

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    headers: {
        "API-KEY": "0b9e5566-c4e7-4525-a65e-5cd0c57bd2e8"
    }
})

export const usersAPI = {
    getUsers(currentPage:number = 1, pageSize:number = 10) {
       return instance.get<getUsersType>(`users?page=${currentPage}&count=${pageSize}`)
       .then(response => response.data)
    },
    
    followUnfollow (id:number, action:string) {
        return instance[action]<followUnfollowType>(`follow/${id}`)
        .then(response => response.data)
     },

    headerLogin () {
        return instance.get<HeaderLoginResponseType>(`auth/me`)
    },

    profileUsers (userId:number) {
        console.warn("Не працює старий метод, ми його перенесли труба йому profileAPI")
        return profileAPI.profileUsers(userId)
    }
   
}

export const profileAPI = {
    profileUsers (userId:number) {
        return instance.get(`profile/` + userId)
    },
    getStatus(userId:number) {
        return instance.get(`profile/status/` + userId)
    },
    updateStatus(status:string) {
        return instance.put(`profile/status`, {status: status})
    },
    savePhoto(file:File) {
        const formData = new FormData();
        formData.append("image", file);
        return instance.put(`profile/photo`, formData, {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
        })
    }
}


export const authAPI = {
    login(email:string, password:string, rememberMe = false, captcha:null|string=null) {
        return instance.post<LoginResponseType>(`auth/login`, {email, password, rememberMe, captcha})
    },
    logout() {
        return instance.delete(`auth/login`)
    },
    getCaptchaUrl() {
        return instance.get(`security/get-captcha-url`) 
    }
}
