import { profileAPI, usersAPI } from "../api/api.ts"
import { photosType, postDataType, profileType } from "../types/types"


let initialState = {
    postData: [
        { id: 1, message: "Hello, how are you?" },
        { id: 2, message: "Tima, hello!" },
      ] as Array<postDataType>,
      profile: null as profileType | null,
      status: "",
}

export type InitialStateType = typeof initialState

const profileReducer = (state = initialState, action:any):InitialStateType => {

    switch (action.type) {
        case "renderPost": 
            return {
                ...state,
                postData: [...state.postData, {id: state.postData.length + 1, message: action.text } ]
            }
        case "setUserProfile":
            return {
                ...state, profile: action.profile
            }
        case "setStatus":
            return {
                ...state, status: action.status
            }
        case "setPhoto":
            return {
                ...state, profile: {...state.profile, photos: action.photo} as profileType // так робити не можна
            }
        case "deletPost":
            return {
                ...state, 
                postData: state.postData.filter(p => p.id != action.postId)
            }
        default:
            return state;
    }  

}

//=======

type renderPostActionType = {
    type: "renderPost", 
    text: string 
}
export const renderPostActionCreater = (text:string):renderPostActionType => {
    return { type: "renderPost", text }
}
//=======
type setUserProfileActionType = {
    type: "setUserProfile", 
    profile: profileType
}
export const setUserProfile = (profile:profileType):setUserProfileActionType => {
    return { type: "setUserProfile", profile }
}
//=======
type setStatusActionType = {
    type: "setStatus"
    status: string 
}
export const setStatus = (status:string):setStatusActionType => {
    return { type: "setStatus", status }
}
//=======
type setPhotoActionType = {
    type: "setPhoto"
    photo: photosType
}
export const setPhoto = (photo:photosType):setPhotoActionType => {
    return { type: "setPhoto", photo}
}
//=======
type deletPostActionType = {
    type: "deletPost"
    postId: number
}
export const deletPost = (postId:number):deletPostActionType => {
    return { type: "deletPost", postId }
}
//=======
export const profileUsersThunk = (userId:number) => {
    return (dispatch:any) => {
        usersAPI.profileUsers(userId)
        .then(response => {
            dispatch(setUserProfile(response.data))
         });
    }
}
export const setStatusThunk = (userId:number) => async (dispatch:any) => {
   let response = await profileAPI.getStatus(userId)
   dispatch(setStatus(response.data))
}

export const updateStatusThunk = (status:string) => async (dispatch:any) => {
    let response = await profileAPI.updateStatus(status)
    if (response.data.resultCode === 0) {
        dispatch(setStatus(status))
    }
}
export const savePhotoThunk = (file:any) => async (dispatch:any) => {
    let response = await profileAPI.savePhoto(file)
    if (response.data.resultCode === 0) {
        dispatch(setPhoto(response.data.data.photos))
    }
}

export default profileReducer