type DialogsType = {
    id: number
    name: string
    class?: string
}
type MessagesType = {
    id: number
    name: string
    message: string
    class?: string
}

let initialState = {
    dialogsData: [
        { id: 1, name: "Dima", class: "dialog-acive" },
        { id: 2, name: "Oleg" },
        { id: 3, name: "Viktor" },
        { id: 4, name: "Jame" }
      ] as Array<DialogsType>,
      messagesData: [
        { id: 1, name: "Dima", message: "Привіт, козаче!" },
        { id: 2, name: "Me", class: 1, message: "ООО, які люди, привіт!" },
        { id: 3, name: "Dima", message: "Як сямаєш?" },
        { id: 4, name: "Me", class: "me", message: "Та все гуд, а ти шо?" }
      ] as Array<MessagesType>
}

export type InitialStateType = typeof initialState

const dialogsReducer = (state = initialState, action: any):InitialStateType => {
    switch (action.type) {
        case "addNewMessage": 
            return {
                ...state,
                messagesData: [...state.messagesData, {id: state.messagesData.length + 1, name: "Me", class: "me", message: action.text}],
            }; 
        default:
            return state;
    }
}

 type AddNewMessageActionType = {
     type: "addNewMessage"
     text: string 
}
export const addNewMessageActionCreater = (text: string): AddNewMessageActionType => {
    return { type: "addNewMessage", text }
}

export default dialogsReducer