import { Dispatch } from "redux"
import { usersAPI } from "../api/api.ts"
import { userDataType } from "../types/types"
import { updateObjectInArray } from "../utilits/helper"
import { InferActionsTypes } from "./redux-store.js"



let initialState = {
    userData: [] as Array<userDataType>,
    pageSize: 5,
    totalItemsCount: 100,
    currentPage: 1,
    isFetching: false,
    followingInProgress: [] as Array<number> // array of users id
}

export type InitialStateType = typeof initialState


const usersReducer = (state = initialState, action: ActionsTypes):InitialStateType => {
    switch (action.type) {
        case "follow": 
        return {...state,
            userData: updateObjectInArray(state.userData, action.userId, "id", {followed: true})
            }
        case "unfollow": 
        return {...state,
            userData: updateObjectInArray(state.userData, action.userId, "id", {followed: false})
           }
        case "setusers":
            return {...state, userData: action.users }
        case "setCurrentPage":
            return {...state, currentPage: action.currentPage }
        case "setTotalCountAC":
            return {...state, totalItemsCount: action.totalCount }
        case "toogleIsFetching":
            return {...state, isFetching: action.isFetching }
        case "followingInProgress":
            return {...state, 
                followingInProgress: action.isFetching
                ? [...state.followingInProgress, action.userId]
                : state.followingInProgress.filter(id => id != action.userId)
            }
        default:
            return state;
    } 
}


type ActionsTypes = InferActionsTypes<typeof actions>

export const actions = {
    follow: (userId:number) => {return {type: 'follow', userId} as const},
    unfollow: (userId:number) => {return {type: 'unfollow', userId} as const},
    setUsers: (users:Array<userDataType>) => {return {type: 'setusers', users} as const},
    setCurrentPage: (currentPage:number) => {return {type: 'setCurrentPage', currentPage} as const},
    setTotalCount: (totalCount:number) => {return {type: 'setTotalCountAC', totalCount} as const},
    setIsFetching: (isFetching:boolean) => {return {type: 'toogleIsFetching', isFetching} as const},
    toogleFollowingInProgress:(isFetching:boolean, userId:number) =>
    {return {type: 'followingInProgress', isFetching, userId} as const}
}


export const getUsersThunkCreator = (currentPage:number, pageSize:number) => async (dispatch: Dispatch<ActionsTypes>) => {
    debugger
    dispatch(actions.setIsFetching(true))
   let response = await usersAPI.getUsers(currentPage, pageSize)
   dispatch(actions.setIsFetching(false))
   dispatch(actions.setUsers(response.items))
   dispatch(actions.setTotalCount(response.totalCount))
}

export const followUnfollowThunkCreator = (propsId:number, serverAction:string, 
    funcAction:(userId:number) => {type: string, userId:number}) => 
    async (dispatch:Dispatch<ActionsTypes>) => {
    dispatch(actions.toogleFollowingInProgress(true, propsId))
    let response = await usersAPI.followUnfollow(propsId, serverAction)
    if (response.resultCode === 0) {
        dispatch(funcAction(propsId))
    }
    dispatch(actions.toogleFollowingInProgress(false, propsId))
}


export default usersReducer