import { authAPI, usersAPI } from "../api/api.ts";

let initialState = {
    id: null as null | number,
    email: null as null | string,
    login: null as null | string,
    isAuth: false,
    error: null as null | string,
    captchaUrl: null as null | string
}
export type InitialStateType = typeof initialState
const authReducer = (state = initialState, action: any): InitialStateType => {
    switch (action.type) {
        case "setUserData": 
       return {
        ...state,
        ...action.data,
       }
       case "setError": // Додаємо action для збереження повідомлення про помилку
      return {
        ...state,
        error: action.error,
      };
      case "setCaptchaUrl": 
      return {
        ...state,
        captchaUrl: action.url,
      };
        default:
            return state;
    }
}
type SetAuthUserDataDataType = {
    id: number|null
    email: string|null
    login: string|null
    isAuth: boolean|null
}
type SetAuthUserDataActionType = {
    type: "setUserData",
    data: SetAuthUserDataDataType
} 

export const setAuthUserData = (id:number|null, email:string|null, 
    login:string|null, isAuth:boolean):SetAuthUserDataActionType => 
{
    return {type: "setUserData", data: {id, email, login, isAuth}} 
}

type setErrorActionType = {
    type: "setError";
    error: string
};
export const setError = (error:string):setErrorActionType => {
    return { type: "setError", error };
};

type setCaptchaUrlActionType = {
    type: "setCaptchaUrl" 
    url: string
}

export const setCaptchaUrl = (url:string):setCaptchaUrlActionType => {
    return { type: "setCaptchaUrl", url };
};


export const headerLoginThunk = () => async (dispatch:any) => {
   let response = await usersAPI.headerLogin()
        if (response.data.resultCode === 0) { // Succsess = 0
            let { id, email, login } = response.data.data
            dispatch(setAuthUserData(id, email, login, true))
        }

}
export const LoginThunk = (email:string, password:string, rememberMe:boolean, captcha:any) => async (dispatch:any) => {
    let response = await authAPI.login(email, password, rememberMe, captcha)
    if (response.data.resultCode === 0) { // Succsess = 0
       return dispatch(headerLoginThunk())
    } else {   
        if (response.data.resultCode === 10) { // CaptchaRequired = 10
            dispatch(getCaptchaThunk())
        } 
        dispatch(setError(response.data.messages));
    }

}
export const getCaptchaThunk = () => async (dispatch:any) => {
    const response = await authAPI.getCaptchaUrl()
    const captchaUrl = response.data.url
    
    dispatch(setCaptchaUrl(captchaUrl));
}
export const LogoutThunk = () => async (dispatch:any) => {
    let response = await authAPI.logout()
    if (response.data.resultCode === 0) { // Succsess = 0
        dispatch(setAuthUserData(null, null, null, false))
    }
}
    


export default authReducer