import React from 'react';
import '../Posts/posts.css'
import NewPost from './NewPost';
import PostFormik from './PostFormik';


const Posts = React.memo(props => { 
    return (
        <div className='post-box'>
            <h4 className='post-title'>My posts</h4>
            <PostFormik {...props}/>
            <div className="posts">
                {props.posts.length 
                ? props.posts.map( item => 
                <NewPost id={item.id} deletPost={props.deletPost} message={item.message} profile={props.profile}/>)
                : <h2 style={{fontWeight: 700, fontSize: "20px"}}>Add post please :)</h2>
                }
                
            </div>
        </div>
    );
});


export default Posts