import '../User/user.css'
import React from 'react';
import ProfileStatusWithHooks from './ProfileStatusWithHooks';
import Preloader from '../common/Preloader';
import { useState } from 'react';
import { profileType, userDataType } from '../../types/types';

type PropsType = {
  savePhotoThunk: (target: any) => any
  profile:profileType
  isOwner:boolean
  users: userDataType
}

function User(props:PropsType) {
  debugger
  const [isLoading, setLoading] = useState<boolean|null>(false)

  const onChousePhoto = (e:any) => {
    if (e.target.files.length) {
      setLoading(true);
      props.savePhotoThunk(e.target.files[0]).then(() => {
        setLoading(false);
      });
    }
  }

  return (
    <div className='user'>
      <div className='user-ava'>
      {props.profile && props.profile.photos.small 
      ? (isLoading ? <Preloader /> : <img src={props.profile.photos.small}/>) 
      : <Preloader /> 
      }
        {props.isOwner && <input className='input-file' type='file' onChange={onChousePhoto}/>}
      </div>
      <div className='user-info'>
        {/* {props.profile.fullName} */}
        <div className='user-name'>Oleg Lis</div>
        <div>
          <ProfileStatusWithHooks {...props} />
          <div className='user-person-info'>Date of Birth:<span>9 May</span></div>
          <div className='user-person-info'>City:<span>Dnipro</span></div>
          <div className='user-person-info'>Education:<span>DDUVS</span></div>
          <div className='user-person-info'>Web Site:<a href='https://github.com/MrBushido21' target='_blank' >https://github.com/MrBushido21</a></div>
        </div>
      </div>
    </div>
  );
}

export default User